/* PROJECTS PAGE */
.container-projects{
    width: 100%;
    height: 90vh;
}

.image-scroll{
    width: 80%;
    height: 50vh;
    margin: 20vh auto 0 auto;
    overflow-y: scroll;
    overflow-x: hidden;
    scrollbar-color: grey white;
    scrollbar-width: thin;
    img{
        max-height: 100%;
        width: auto;
    }
}

.responsive{
    margin: 18px 0 0 18px;
}

